import React, { Component } from 'react';
import { Card, CardBody, CardHeader } from 'reactstrap';

export default class FactsCard extends Component{
    static defaultProps = {
       // title: 'Facts At a Glance'
       title: 'test title',
       contents : []
    }
    render(){
        const rows = this.props.contents.map((content)=>{
            return (
                <>
                <dt className="col-6">{content.name}</dt>
                <dd className="col-6">{content.value}</dd>
                </>
            );

        });
        return (
            <Card>
                <CardHeader className="bg-primary text-white">{this.props.title}</CardHeader>
                <CardBody>

                    <dl className="row p-1">
                        {rows}
                    </dl>
                </CardBody>
            </Card>
        );
    }
}


