import React ,{ Component } from 'react';
import { Card, CardImg, CardText, CardTitle, CardBody, Breadcrumb, BreadcrumbItem ,Modal, ModalHeader, ModalBody, Label, Button, Row, Col } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Errors, LocalForm, Control } from 'react-redux-form';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !(val) || (val.length <= len);
const minLength = (len) => (val) => val && (val.length>=len);

function RenderDish({dish}){
  
      return (
        <div>
            <FadeTransform
            in transformProps={{
                exitTransform: 'scale(0.5) translateY(-50%)'
            }}>
          <Card>
            <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name}/>
                <CardBody>
                    <CardTitle>{dish.name}</CardTitle>
                    <CardText>{dish.description}</CardText>
                </CardBody>
          </Card>
          </FadeTransform>
         </div>
      );
    
}

function RenderComments({comments, postComment, dishId}){
    if(comments != null)
        return(
            <div className="col-12 col-md-5 m-1">
                <h4>Comments</h4>
                <ul className="list-unstyled">
                    <Stagger in >
                    {comments.map((comment) => {
                        return (
                            <Fade in>
                            <li key={comment.id} >
                                <p>{comment.comment}</p>
                                <p>-- {comment.author}, {new Intl.DateTimeFormat('en-US',{year:'numeric',month:'short',day:'2-digit'}).format(new Date(comment.date))}</p>
                            </li>
                            </Fade>
                        );
                    })}
                    </Stagger>
                </ul>
               
                <CommentForm dishId={dishId} postComment={postComment} />
            </div>
        )
        else
          return (
                <div className="col-12 col-md-5 m-1">
                <h4>Comments</h4>
                </div>
          )
        
}

      
class CommentForm extends Component {
    constructor(props){
        super(props);
        this.state ={
            isModalOpen: false
        };
        this.toggleModal = this.toggleModal.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    toggleModal(){
        this.setState({
            isModalOpen : !this.state.isModalOpen
        });
    }
    handleSubmit(values){
        //alert("Submitted values are :" + JSON.stringify(values));
        this.props.postComment(this.props.dishId, values.rating, values.author, values.comment);
        this.toggleModal();
    }
    /*closeModal (){
        this.setState({
            isModalOpen : false
        });
    }*/
    
    render(){
        return(
            <div>
                <Button outline onClick={this.toggleModal}>
                     <span className="fa fa-lg fa-pencil"> Submit Comment</span>
                </Button>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal} >
                    <ModalHeader toggle={this.toggleModal}>
                        <h4>
                            Submit Comment
                        </h4>                                               
                    </ModalHeader>
                    <ModalBody>
                        <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
                            <Row className="form-group">
                                <Col md={12}>
                                    <Label htmlFor="rating">Rating</Label>
                                </Col>
                                <Col md={12}>
                                    <Control.select model=".rating" id="rating" name="rating" className ="form-control">
                                        <option value="1">1</option>
                                        <option value="2">2</option>
                                        <option value="3">3</option>
                                        <option value="4">4</option>
                                        <option value="5">5</option>
                                    </Control.select>
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Col md={12}>
                                    <Label htmlFor="author">Your Name</Label>
                                    
                                </Col>
                                <Col md={12}>
                                    <Control.text model=".author" 
                                    id="author" name="author" placeholder="Your Name"
                                    className="form-control"
                                    validators ={{
                                        required, maxLength: maxLength(15), minLength: minLength(3)
                                    }}
                                    />
                                    <Errors
                                    className="text-danger"
                                    model=".author"
                                    show="touched"
                                    messages = {{
                                        required: 'Required',
                                        maxLength: 'Must be 15 characters or less',
                                        minLength : 'Must be greater than 2 characters'
                                    }}
                                     />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Col md={12}>
                                    <Label htmlFor="comment">Comment</Label>
                                </Col>
                                <Col md={12}>
                                    <Control.textarea model=".comment" id="comment" name="comment" rows="6"
                                    className="form-control" />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Col md={{size:12}}>
                                    <Button color="primary" type-="submit" value="submit">
                                        Submit
                                    </Button>
                                </Col>
                            </Row>
                            
                        </LocalForm>
                    </ModalBody>
                    
                </Modal>
            </div>
        )
    }

}
  
const  DishDetail = (props) => {
    if(props.isLoading){
        return(
            <div className="container">
                <div className="row">
                    <Loading />
                </div>
            </div>
        );
    }
    else if(props.errMess){
        return(
            <div className="container">
                <div className="row">
                    <h4>{props.errMess}</h4>
                </div>
            </div>
        );
    }
    else if(props.dish!=null)
    {
        return (
            <div className="container">
            <div className="row">
                <Breadcrumb>

                    <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                    <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                </Breadcrumb>
                <div className="col-12">
                    <h3>{props.dish.name}</h3>
                    <hr />
                </div>                
            </div>
            <div className="row">
                <div className="col-12 col-md-5 m-1">
                    <RenderDish dish={props.dish} />
                </div>
                
                    <RenderComments 
                    comments={props.comments} 
                    postComment ={props.postComment}
                    dishId = {props.dish.id}
                    />
                    
                
            </div>
            </div>
        );
     }
     else{
         return(
             <div></div>
         )
     }
          
   }
export default DishDetail;